.btn {
  font-family: "Gotu", sans-serif;
  font-size: 0.875rem;
  display: inline-block;
  width: 12.625rem;
  height: 3.375rem;
  background-color: #408F95;
  border: 1px solid #408F95;
  color: #FFFFFF;
  letter-spacing: 0;
  line-height: 1;
  transition: 0.3s;
  cursor: pointer;
  position: relative;
}

.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background: #FFFFFF;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.3s;
  transition-property: transform;
}

@media screen and (min-width: 768px) {
  .btn:hover::before {
    transform: scale(1, 1);
  }
}

.btn::after {
  content: "";
  position: absolute;
  display: block;
  background-color: transparent;
  border: 1px solid #408F95;
  width: 12.625rem;
  height: 3.375rem;
  bottom: -0.3125rem;
  left: 0.25rem;
  z-index: -1;
}

.btn span {
  display: block;
  height: 110%;
  width: 105%;
  padding: 1.1875rem 4.375rem 1.1875rem 0.6875rem;
  position: relative;
}

@media screen and (min-width: 768px) {
  .btn:hover span {
    color: #408F95;
    opacity: 1;
  }
}

.btn span::before {
  content: "";
  position: absolute;
  display: block;
  background-color: #FFFFFF;
  width: 2.5rem;
  height: 0.0625rem;
  top: 48%;
  right: 3.125rem;
  transform: translateY(-50%);
  transition: 0.3s;
  border-radius: 10px;
}

@media screen and (min-width: 768px) {
  .btn span:hover::before {
    background-color: #408F95;
    opacity: 1;
  }
}

.btn span::after {
  content: "";
  position: absolute;
  display: block;
  background-color: #FFFFFF;
  width: 0.625rem;
  height: 0.0625rem;
  right: 3.0625rem;
  top: 41%;
  transform: rotate(45deg);
  transition: 0.3s;
  border-radius: 10px;
}

@media screen and (min-width: 768px) {
  .btn span:hover::after {
    background-color: #408F95;
    opacity: 1;
  }
}

